import Vue from "vue";
import VueRouter from "vue-router";
// 1. 定义 (路由) 组件。
// 可以从其他文件 import 进来
import Home from "../../components/game-index.vue";

Vue.use(VueRouter);

// 2. 定义路由
// 每个路由应该映射一个组件。
const routes = [{
		path: "/",
		name: "PGame",
		component: Home,
	},
	// {
	// 	path: "/verify",
	// 	name: "verify",
	// 	// 路由懒加载
	// 	component: () =>
	// 		import("../../components/verify.vue"),
	// },
	// {
	// 	path: "/verifyDetails",
	// 	name: "verifyDetails",
	// 	// 路由懒加载
	// 	component: () =>
	// 		import("../../components/verify-details.vue"),
	// },
];

// 3. 创建 router 实例，然后传 `routes` 配置
const router = new VueRouter({
	// mode: "history",
	// base: process.env.BASE_URL,
	routes,
});

export default router;