let domSvg = document.getElementById("initial-loader-1");
var alpha = 1;
var timer = 0;
let progressBarNum = 0

var progressBarBg = document.getElementById('progress-bar-bg-cover');
var progressBarContent = document.getElementById('progress-bar-content');
var progressBar = document.getElementById('progress-bar-fill-stripes');
var progressBarLen = 0;
var progressBarAdd = null;
var idFadeInOut = false;

function fadeInOut(value) {
	if(idFadeInOut) return;
	idFadeInOut = true;
	clearInterval(timer);
	progressBar.style.width = '0%'
	timer = setInterval(
		function() {
			alpha -= .1;
			domSvg.style.opacity = alpha;
			progressBarBg.style.opacity = alpha;
			if (alpha <= 0) {
				clearInterval(timer);
				domSvg.remove()
				showLogo()
			}
		}, 100);
}

var progressBarTime = setInterval(
	function() {
		progressBarLen += .5
		progressBarContent.style.width = progressBarLen + '%'
		progressBarColor(progressBarLen)
		if (progressBarLen >= 70) {
			clearInterval(progressBarTime);
		}
	}, 16);

window.onload = function() {
	clearInterval(progressBarTime);
	var progressBarTime1 = setInterval(
		function() {
			progressBarLen += .6
			progressBarContent.style.width = progressBarLen + '%'
			progressBarColor(progressBarLen)
			if (progressBarLen >= 100) {
				clearInterval(progressBarTime1);
				progressBarBg.style.opacity = 0
				progressBarBg.style.display = 'none'
				progressBarBg.remove()
				setTimeout(() => {
					fadeInOut();
				}, 1000);
			}
		}, 16);
}

window.fadeInOut = function() {
	fadeInOut();
}



function progressBarColor(num) {
	if (num <= 40) {
		progressBarContent.style.backgroundColor = "#FFF100"
	} else if (num <= 60) {
		progressBarContent.style.backgroundColor = "#c8e35a"
	} else if (num <= 80) {
		progressBarContent.style.backgroundColor = "#9de36f"
	} else {
		progressBarContent.style.backgroundColor = "#59e390"
	}
}

function progressBarAnimation() {
	let progressBar = document.getElementById('progress-bar-fill-stripes');
	let textChild = document.getElementById('tips-text-child2');


	if (parseFloat(progressBar.style.width) < 50) {
		progressBarAdd = setInterval(
			function() {
				progressBarNum += 0.2
				progressBar.style.width = progressBarNum + "%";
				textChild.innerHTML = "[" + progressBarNum + "%]"

				let loadingGameText = "A carregar jogo"
				let readingInformation = "A ler informações"

				const lang = window.i18n ? window.i18n.locale : 'pt'
				if (lang == "pt") {
					loadingGameText = "A carregar jogo";
					readingInformation = "A ler informações";
				} else {
					loadingGameText = "Loading game";
					readingInformation = "Reading information";
				}

				if (progressBarNum >= 45) {
					tipsSpanEle.textContent = readingInformation;
				} else {
					tipsSpanEle.textContent = loadingGameText + " [" + Math.ceil(progressBarNum) + "%]";
				}

				if (progressBarNum >= 50) {
					clearInterval(progressBarAdd);
				}
			}, 16);
	}
}

