function loadingLanguage() {
	let url = new URL(window.location.href);
	let params = new URLSearchParams(url.search);
	let param = params.get('lang'); // 返回 'value1' 
	// let request = new XMLHttpRequest();
	// request.open("GET", "../assets/js/label.json", false);
	// request.send(null);
	let request = require("./label.json");
	let jsonData = request[param];
	if (!jsonData) {
		param = "pt"
		jsonData = request["pt"]
	}
	const lang = window.i18n ? window.i18n.locale : 'pt'
	if (lang == "pt") {
		param = "pt"
		jsonData = request["pt"]
	} else {
		param = "en"
		jsonData = request["en"]
	}
	mAdsContArr = jsonData.shared_AnimationTips;
	loginFail = jsonData.login_fail;
	tipsSpanEle.textContent = jsonData.loading_game_res;
	animatedTextEle.textContent = jsonData.loading_tips3;
	getStartedText.textContent = jsonData.shared_record_start;
	beginLoginText = jsonData.begin_login;
	loadingText = jsonData.loading_res;
	// loadingBg.style.backgroundImage = `url(./assets/image/` + param + `/startup.jpg)`;
	// loadingbt.src = `./assets/image/` + param + `/copyright.png`;
	console.log("param", param, jsonData);
}
loadingLanguage();

function setTip() {
	// Login failed, system retrying... (1st time)
	eleSetCount++
	// tipsSpanEle.textContent = "Login failed, system retrying...(" + eleSetCount + "st time)";
	tipsSpanEle.textContent = loginFail.replace("%", eleSetCount)
}
