let mAdsContLen = mAdsContArr.length;
let mAdsInd = 0;
let mShowState = 1;
let textAnimIntervalId = setInterval(() => {
	if (1 == mShowState) {
		animatedTextEle.classList.remove("animated_text_move_to_center");
		animatedTextEle.classList.add("animated-text-move-to-top-port");
		mShowState = 2;
	} else if (2 == mShowState) {
		animatedTextEle.classList.remove("animated-text-move-to-top-port");
		mShowState = 3;
		animatedTextEle.textContent = mAdsContArr[mAdsInd++];
		if (mAdsInd >= mAdsContLen) {
			mAdsInd = 0;
		}
	} else if (3 == mShowState) {
		animatedTextEle.classList.add("animated_text_move_to_center");
		mShowState = 4;
	} else if (4 == mShowState) {
		mShowState = 1;
	}
}, 1100);



