let valueTmp = 0

window.beginLogin = function beginLogin() {

	tipsSpanEle.textContent = beginLoginText
}

window.beginGame = function beginGame() {
	tipsSpanEle.textContent = "Completar";
}

window.showBeginBtn = function showBeginBtn() {
	const startBtn = document.getElementById('__startedButton');

	startBtn.style.visibility = 'visible';
	loadingContainer.style.display = 'none';
	animContainerEle.style.visibility = 'hidden';
	clearInterval(tipsSpanEleSetinterEnd)
}

window.updateProgressNoText = function updateProgressNoText(value) {
	clearInterval(tipsSpanEleSetinter)
	tipsSpanEleSetinter = setInterval(setTip, 30000);

	valueTmp = Math.max(valueTmp, value)
	let percentNum = Math.ceil(valueTmp * 100)
	// clearInterval(progressBarAdd);
	if (progressBarNum < percentNum) {
		progressBar.style.width = percentNum + "%";
	}
}
updateProgressNoText(0.1)

window.updateProgress = function updateProgress(value) {
	clearInterval(tipsSpanEleSetinter)
	tipsSpanEleSetinter = setInterval(setTip, 30000);

	valueTmp = Math.max(valueTmp, value)
	let percentNum = Math.ceil(valueTmp * 100)
	// console.log("progess:", value);
	// clearInterval(progressBarAdd);
	if (progressBarNum < percentNum) {
		progressBar.style.width = percentNum + "%";
	}


	tipsSpanEle.textContent = loadingText + " [" + percentNum + "%]";
	if (value >= 1) {
		beginGame();
		clearInterval(tipsSpanEleSetinterEnd)
		clearInterval(tipsSpanEleSetinter)
		tipsSpanEleSetinterEnd = setInterval(showBeginBtn, 300);
	}
}


