function onsize() {
	let splash = document.getElementById('splash')
	let gameHeader = document.getElementById('game-header-holder')
	let newacle = window.innerWidth / window.innerHeight
	console.log("newacle:", newacle, "  innerWidth:", window.innerWidth, "  innerHeight:", window.innerHeight);


	// var copyright = document.getElementById("footer-copyright-image-id");
	// copyright.style.bottom = "0.5px"

	if (newacle < (414 / 896)) { //0.46205357142857145
		// console.log("1111");
		splash.style.height = "790px"
		gameHeader.style.height = "790px"
	} else if (newacle > 0.5555555) {
		// console.log("2222");
		splash.style.height = "640px"
		gameHeader.style.height = "640px"
	} else if (newacle == (360 / 740)) { //0.4864864864864865
		// console.log("3333");
		splash.style.height = "740px"
		gameHeader.style.height = "740px"
	} else if (newacle == (414 / 896) || newacle == (390 / 844) ||
		newacle == (430 / 932) || newacle == (412 / 915)) {
		// 0.46205357142857145  0.46208530805687204  0.4613733905579399  0.45027322404371584
		// console.log("4444");
		splash.style.height = "780px"
		gameHeader.style.height = "780px"
	} else {
		// 0.461118690313779
		// console.log("else");
		splash.style.height = 1456 * (1 - newacle) + "px"
		gameHeader.style.height = 1456 * (1 - newacle) + "px"
		// copyright.style.bottom = "10px"
	}
}


