<style id="style-mobile">
html {
	-ms-touch-action: none;
}

body,
canvas,
div {
	display: block;
	outline: none;
	-webkit-tap-highlight-color: rgba(0, 0, 0, 0);

	user-select: none;
	-moz-user-select: none;
	-webkit-user-select: none;
	-ms-user-select: none;
	-khtml-user-select: none;
	-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
	-webkit-appearance: none;
	margin: 0;
}

body {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	padding: 0;
	border: 0;
	margin: 0;

	cursor: default;
	color: #000000;
	background-color: #333;

	text-align: center;
	font-family: Helvetica, Verdana, Arial, sans-serif;

	display: flex;
	flex-direction: column;
	overflow: hidden;
}

canvas {
	background-color: rgba(0, 0, 0, 0);
}

a:link,
a:visited {
	color: #666;
}

a:active,
a:hover {
	color: #666;
}

p.header {
	font-size: small;
}

p.footer {
	font-size: x-small;
}

#splash {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-size: 45%;
	background-position: 50%;
	background-size: cover;
}

.progress-bar {
	position: absolute;
	left: 27.5%;
	top: 80%;
	height: 3px;
	padding: 2px;
	width: 45%;
	border-radius: 7px;
	box-shadow: 0 1px 5px #000 inset, 0 1px 0 #444;
}

.progress-bar span {
	display: block;
	height: 100%;
	border-radius: 3px;
	transition: width .4s ease-in-out;
	background-color: #3dc5de;
}

.stripes span {
	background-size: 30px 30px;
	background-image: linear-gradient(135deg, rgba(255, 255, 255, .15) 25%, transparent 25%,
			transparent 50%, rgba(255, 255, 255, .15) 50%, rgba(255, 255, 255, .15) 75%,
			transparent 75%, transparent);

	animation: animate-stripes 1s linear infinite;
}

@keyframes animate-stripes {
	0% {
		background-position: 0 0;
	}

	100% {
		background-position: 60px 0;
	}
}

#background-img {
	background-position: 50%;
}
</style>
<style id="style">
body {
	cursor: default;
	padding: 0;
	border: 0;
	margin: 0;

	display: flex;
	flex-direction: column;
	height: 100%;
	/* for firefox */
	width: 100%;
	position: absolute;

	text-align: center;
	background-color: white;
	font-family: Helvetica, Verdana, Arial, sans-serif;
}

canvas {
	background-color: rgba(0, 0, 0, 0);
}

#GameCanvas {
	width: 100%;
	height: 100%;
	position: absolute;
}
</style>
<style id="initial-style">
body {
	height: 100vh;
	margin: 0;
	width: 100vw
}

#game-shell {
	display: flex;
	height: 100%;
	position: fixed;
	width: 100%
}

#game-box {
	position: absolute;
	width: 100%;
	height: 100%;
}

#GameCanvas {
	width: 100%;
	height: 100%;
	position: absolute;
}

#game-overlay {
	height: 0;
	position: absolute;
	width: 0
}

#background-img {
	background-size: cover;
	/* bottom: -10%; */
	height: 100%;
	left: 0;
	position: absolute;
	right: 0;
	top: 0;
	width: 100%
}
</style>
<style id="progress-bar-style">
#progress-bar-bg-cover {
	width: 100vw;
	height: 100vh;
	background-color: #000;
	position: fixed;
	z-index: 999;
}

.progress-bar-bg {
	background-color: #4d4900;
	width: 100vw;
	height: .6vh;
	min-height: 5px;
	z-index: 10;
	position: fixed;
}

#progress-bar-content {
	background: #59e390;
	width: 0%;
	height: .6vh;
	min-height: 5px;
}

.progress-bar-image {
	position: fixed;
	transform: translateY(-50%) translateX(-50%);
	top: 50%;
	left: 50%;
	z-index: 9;
}

@media all and (orientation : portrait) {
	.progress-bar-image {
		width: 100vw;
		height: 100vw;
	}
}
</style>
<style id="loader-style">
#initial-loader-1 {
	background-color: #000;
	margin: auto;
	position: fixed;
	width: 100vw;
	z-index: 98;
	height: 100vh;
	top: 0;
	left: 0;
}

.initial-loader-img {
	position: fixed;
	transform: translateY(-50%) translateX(-50%);
	top: 50%;
	left: 50%;
}

@media all and (orientation : portrait) {
	.initial-loader-img {
		width: 100vw;
		height: 100vw;
	}
}

@media all and (orientation : landscape) {
	.initial-loader-img {
		width: 50vw;
		height: 50vw;
	}
}

#initial-loader-1 {
	align-items: center;
	display: flex;
	flex-direction: column;
	justify-content: center
}
</style>
<style id="shell-css">
body,
canvas,
div {
	-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
	display: block;
	outline: none;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none
}

body {
	background: #000;
	-ms-scroll-chaining: none;
	height: 100vh;
	margin: 0;
	overscroll-behavior: contain;
	width: 100vw
}

canvas {
	background-color: transparent
}

a:active,
a:hover,
a:link,
a:visited {
	color: #666
}

p.header {
	font-size: small
}

p.footer {
	font-size: x-small
}

.game-shell {
	font-family: PingFang SC, Microsoft YaHei, WenQuanYi Micro Hei, sans-serif;
	touch-action: none
}

.screen_compat {
	height: auto;
	margin: auto;
	position: absolute;
	width: 360px
}

.screen_safe_area {
	height: 640px;
	width: 360px;
	z-index: 0;

}

.screen_safe_area {
	bottom: 0;
	left: 0;
	margin: auto;
	position: absolute;
	right: 0;
	top: 0
}

.screen_color {
	background-color: #000
}

.lobby .screen_color {
	background-color: #fff
}


#tips-text {
	/* margin-top: 4px; */
	text-align: center;
	text-overflow: ellipsis;
	width: 90%;
	font-family: PingFang SC, Microsoft YaHei, WenQuanYi Micro Hei, sans-serif;
	line-height: 1;
}

.tips-text-child2-hidden {
	display: none
}

.tips-text-child2 {
	margin-left: 5px
}


.start-button-container-port {
	background-color: rgb(195, 32, 182);
	text-shadow: rgb(195, 32, 182) 0px 1px 2px;
	visibility: hidden;
	align-items: center;
	display: flex;
	justify-content: center;
	left: 0;
	margin: auto;
	position: absolute;
	right: 0;
	height: 32px;
	top: -170px;
	width: 151.7px
}

.start-button-container-land {
	background-color: rgb(195, 32, 182);
	text-shadow: rgb(195, 32, 182) 0px 1px 2px;
	visibility: hidden;
	align-items: center;
	display: flex;
	justify-content: center;
	left: 0;
	margin: auto;
	position: absolute;
	right: 0;
	height: 30px;
	top: -80px;
	width: 180px
}

.start-button {
	background-color: #30a2d0;
	border: 2px solid rgba(0, 0, 0, .15);
	border-radius: 8px;
	text-shadow: 0 2px 3px #30a2d0
}


.start-button-show-port {
	animation-name: show-bounce
}

.start-button-inner {
	background-image: linear-gradient(180deg, hsla(0, 0%, 100%, .5), hsla(0, 0%, 100%, 0));
	background-origin: border-box;
	border: .87px solid hsla(0, 0%, 100%, .4);
	border-radius: 6px;
	bottom: 0;
	left: 0;
	position: absolute;
	right: 0;
	top: 0
}

@keyframes show-bounce {
	0% {
		transform: scale(0)
	}

	20% {
		transform: scale(1.2)
	}

	50% {
		transform: scale(.98)
	}

	to {
		transform: scale(1)
	}
}

@keyframes show-bounce-land {
	0% {
		transform: scale(0)
	}

	20% {
		transform: scale(.84)
	}

	50% {
		transform: scale(.68)
	}

	to {
		transform: scale(.7)
	}
}

.text-land,
.text-port {
	color: #fff;
	font-size: 10.3px;
	margin: 0;
	padding: 0
}

.text-land {
	transform: scale(.8)
}

.start-button .text-land,
.start-button,
.start-button .text-port {
	font-size: 12px;
	font-weight: 900
}

.dark .text-port,
.lobby .text-port {
	color: #000
}


.animationTipsContainer-port {
	align-items: center;
	display: flex;
	flex-direction: column;
	height: 35px;
	position: relative;
	width: 100%;
	margin-top: -140px;
	z-index: 1
}

.animationTipsContainer-land {
	align-items: center;
	display: flex;
	flex-direction: column;
	height: 35px;
	position: relative;
	width: 100%;
	margin-top: -65px;
	z-index: 1
}

.animated_text_wrap {
	color: #fff;
	font-size: 10px;
	height: 26px;
	line-height: 26px;
	position: relative;
	text-align: center;
	width: 100%
}

.dark .animated_text_wrap,
.lobby .animated_text_wrap {
	color: #000
}

.animated_text {
	align-items: center;
	display: flex;
	height: 26px;
	justify-content: center;
	line-height: 13px;
	margin: 0;
	opacity: 0;
	position: absolute;
	top: 100%;
	width: 100%
}

.animated-text-reset-to-bottom-port {
	opacity: 0;
	top: 26px;
	font-size: 12px;
}

.animated-text-reset-to-bottom-land {
	opacity: 0;
	top: 26px;
	font-size: 10px;
}

.animated_text_move_to_center {
	transition: top 1s, opacity 1s;
	transition-timing-function: linear;
	opacity: 1;
	top: 0
}

.loading-container-port {
	align-items: center;
	display: flex;
	flex-direction: column;
	left: 0;
	position: absolute;
	right: 0;
	top: -200px
}

.loading-container-land {
	align-items: center;
	display: flex;
	flex-direction: column;
	left: 0;
	position: absolute;
	right: 0;
	top: -85px;
}



.progress-bar-container-port {
	background-color: initial;
	height: 16px;
	position: relative;
	width: 220px
}

.progress-bar-container-land {
	background-color: initial;
	height: 12px;
	position: relative;
	width: 250px
}

.progress-bar-background {
	background-color: #111;
	border-radius: 5px;
	height: 100%;
	position: absolute;
	width: 100%
}

.progress-bar-outline {
	border-radius: 5px;
	bottom: 0;
	left: 0;
	position: absolute;
	right: 0;
	top: 0;
	transform: translateZ(0)
}

.border-inner {
	border: 1.7px solid #272727
}

.border-outer {
	border: .85px solid #111
}

.progress-bar-fill-container {
	bottom: 1.5px;
	left: 1px;
	position: absolute;
	right: 1px;
	top: 1px;
}

.progress-bar-fill {
	-webkit-backface-visibility: hidden;
	backface-visibility: hidden;
	background-color: #30a2d0;
	background-size: 8.7px 100%;
	border-radius: 3.5px;
	height: 100%;
	position: absolute;
	width: 0
}

.stripes {
	animation-duration: 1s;
	animation-iteration-count: infinite;
	animation-name: animate-stripes;
	animation-timing-function: linear;
	background-image: linear-gradient(-75deg, hsla(0, 0%, 100%, 0) 35%, hsla(0, 0%, 100%, .1) 0, hsla(0, 0%, 100%, .1) 75%, hsla(0, 0%, 100%, 0) 0, hsla(0, 0%, 100%, 0))
}

.front-highlight {
	background-image: linear-gradient(90deg, hsla(0, 0%, 100%, 0), #fff);
	border-radius: 0 3.5px 3.5px 0;
	height: 100%;
	max-width: 20px;
	right: 0;
	width: 50%
}

.front-highlight,
.top-highlight {
	position: absolute;
	transform: translateZ(0)
}

.top-highlight {
	background-color: hsla(0, 0%, 100%, .2);
	border-radius: 3.5px 3.5px 0 0;
	height: 50%;
	width: 100%
}

@keyframes animate-stripes {
	0% {
		background-position: 0 0
	}

	to {
		background-position: 34.7px 0
	}
}

.lobby .progress-bar-background {
	background-color: #f5f5f5
}

.lobby .border-outer {
	display: none
}

.lobby .border-inner {
	border: 1.7px solid #f5f5f5
}

.lobby .front-highlight {
	opacity: .5
}

.lobby .top-highlight {
	display: none
}


.footer-container {
	display: flex;
	height: 77px
}

.footer-container,
.footer-mask-container-port {
	bottom: 0;
	position: absolute;
	width: 100%
}

.footer-mask-container-port {
	display: flex;
	flex-direction: column;
	height: 219.3px
}

.footer-mask {
	height: 100%;
	position: absolute;
	width: 100%
}

.footer-mask-black {
	background-image: linear-gradient(180deg, rgba(0, 0, 0, .03), #000)
}

.footer-mask-color {
	background-image: linear-gradient(180deg, hsla(0, 0%, 100%, 0), #fff)
}

.footer-container img {
	height: 117px;
	width: 100%
}

.footer-mask-container-port {
	display: none
}
</style>
<style id="notify-css">
.loading_circle_container,
.loading_container,
.loading_holder,
.loading_panel {
	bottom: 0;
	left: 0;
	position: absolute;
	right: 0;
	top: 0
}

.loading_holder,
.loading_panel {
	height: auto;
	overflow: hidden;
	width: 360px
}

.loading_holder {
	max-height: 780px;
	min-height: 640px;
	z-index: 900
}

.loading_panel {
	top: 800px
}

.loading_container {
	height: inherit;
	margin: auto;
	min-height: inherit;
	min-width: inherit;
	overflow: hidden;
	text-align: center;
	width: inherit
}

.loading_background {
	height: 100%;
	width: 100%
}

.loading_font {
	bottom: 0;
	color: #30a2d0;
	font-size: 15.7px;
	left: 0;
	line-height: 21.7px;
	margin: auto;
	max-height: 65px;
	overflow: hidden;
	padding-left: 7%;
	padding-right: 7%;
	position: absolute;
	right: 0;
	top: 52px;
	width: 86%
}

.loading_circle_container {
	align-items: center;
	bottom: 34.7px;
	display: flex;
	height: 8.7px;
	justify-content: space-between;
	margin: auto;
	position: absolute;
	width: 26px
}

.loading_circle {
	animation-direction: alternate;
	animation-duration: .25s;
	animation-iteration-count: infinite;
	animation-name: loading_circle_bounce;
	animation-timing-function: ease-out;
	background-color: #30a2d0;
	border-radius: 50%;
	height: 6px;
	position: relative;
	width: 6px
}

.loading_circle:first-of-type {
	animation-delay: 0s
}

.loading_circle:nth-of-type(2) {
	animation-delay: -75ms
}

.loading_circle:nth-of-type(3) {
	animation-delay: -.15s
}

@keyframes loading_circle_bounce {
	0% {
		bottom: 0
	}

	90%,
	to {
		bottom: 13px
	}
}

@keyframes loading_circle_bounce_landscape {
	0% {
		bottom: 0
	}

	90%,
	to {
		bottom: 7px
	}
}

.footer-text-img {
	transition: .2s;
}

img {
	overflow-clip-margin: content-box;
	overflow: clip;
}

button .text-port {
	font-size: 12px;
	font-weight: 900;
}

#game-header-holder {
	/* display: none; */
}

.game-title {
	width: 300px;
	height: 17px;
	position: absolute;
	display: flex;
	color: white;
	opacity: 0.85;
	/* text-shadow: rgb(65, 50, 24) 1px 0px 0px, rgb(65, 50, 24) 0.552px 0.85px 0px, rgb(65, 50, 24) -0.4px 0.9px 0px, rgb(65, 50, 24) -0.1px 0.15px 0px, rgb(65, 50, 24) -0.65px -0.7px 0px, rgb(65, 50, 24) 0.3px -0.95px 0px, rgb(65, 50, 24) 0.96px -0.28px 0px; */
	pointer-events: none;
	white-space: nowrap;
	z-index: 250;
	/* margin-top: 10px; */
}

.time_stamp {
	height: 17px;
	position: absolute;
	display: flex;
	color: white;
	opacity: 0.85;
	/* text-shadow: rgb(65, 50, 24) 1px 0px 0px, rgb(65, 50, 24) 0.552px 0.85px 0px, rgb(65, 50, 24) -0.4px 0.9px 0px, rgb(65, 50, 24) -0.1px 0.15px 0px, rgb(65, 50, 24) -0.65px -0.7px 0px, rgb(65, 50, 24) 0.3px -0.95px 0px, rgb(65, 50, 24) 0.96px -0.28px 0px; */
	pointer-events: none;
	font-family: monospace;
	z-index: 250;
	/* margin-top: 10px; */
}

.maskAnimate {
	animation: animate .5s ease;
	animation-fill-mode: forwards;
}

.upendMaskAnimate {
	animation: upendAnimate .5s ease;
	animation-fill-mode: forwards;
}

@keyframes animate {
	from {
		transform: translateY(100%);
		display: none;
	}

	to {
		transform: translateY(0%);
		display: block;
	}
}

@keyframes upendAnimate {
	from {
		transform: translateY(0%);
		display: block;
	}

	to {
		transform: translateY(100%);
		display: none;
	}
}

.showAnimate {
	animation: leftAnimate .75s ease;
	animation-fill-mode: forwards;
}


.hideAnimate {
	animation: rightAnimate .75s ease;
	animation-fill-mode: forwards;
}



@keyframes leftAnimate {
	from {
		transform: translateX(100%);
		display: none;
	}

	to {
		transform: translateX(0%);
		display: block;
	}
}

@keyframes rightAnimate {
	from {
		transform: translateX(0%);
		display: block;
	}

	to {
		transform: translateX(100%);
		display: none;
	}
}
</style>

<template>
	<div>
		<div id="progress-bar-bg-cover">
			<div class="progress-bar-bg">
				<div id="progress-bar-content"></div>
			</div>
			<img ref="animatedGif" class="progress-bar-image" :src="`${imgUrl}/${this.brandName}/入場.webp`" alt=""
				srcset="" />
		</div>
		<div id="game-shell">
			<div id="background-img"></div>
		</div>

		<div id="game-box">
			<canvas id="GameCanvas" oncontextmenu="event.preventDefault()" tabindex="0"></canvas>
		</div>

		<div id="game-overlay">
			<div v-if="showLoading" :class="`loading_holder ${animationLoading ? 'maskAnimate' : ''}`"
				style="width: 0px; height: 0px; overflow: unset;">
				<div class="loading_panel" id="loading_panel"
					:style="`opacity: 1; width: 360px; left: 0px; top: 0px; transform: scale(1);height:${configurationHeight}px;`">
					<div class="loading_background" style="background-color: rgb(49, 49, 61);">
					</div>
					<div class="loading_container">
						<div class="loading_circle_container">
							<div class="loading_circle" style="background-color: rgb(255, 200, 36);"></div>
							<div class="loading_circle" style="background-color: rgb(255, 200, 36);"></div>
							<div class="loading_circle" style="background-color: rgb(255, 200, 36);"></div>
						</div>
						<div class="loading_font" style="color: rgb(255, 200, 36);">A carregar</div>
					</div>
				</div>
			</div>

			<div id="game-header-holder"
				style="z-index: 999; pointer-events: none; position: absolute;top:0; width: 360px; visibility: visible;">
				<div id="game-title" class="game-title"
					style="visibility: visible; justify-content: flex-start; width: 300px; height: 18px; font-size: 11.5px;">
					<div style="padding-left: 12px; display: flex; align-items: center;">{{ gameInfo.name }}</div>
				</div>
				<div id="time-stamp" class="time_stamp"
					style="justify-content: flex-end; height: 18px; font-size: 11.5px; visibility: visible; width: 360px;">
					<div style="padding-right: 12px; display: flex; align-items: center;">{{ timeStamp }}</div>
				</div>
			</div>
			<div id="splash" class="screen_color screen_compat" :style="splashStyle">
				<div id="footer-container" class="footer-container" style="height: 201.52px;">
					<div class="footer-mask-container-port">
						<div class="footer-mask footer-mask-color"
							style="background-image: linear-gradient(180deg, rgba(195, 32, 182, 0), #C320B6)">
						</div>
						<div class="footer-mask footer-mask-black"> </div>
					</div>
				</div>
				<!-- <div class="screen_safe_area">
				</div> -->
				<div :style="{
					width: 'inherit',
					height: '20px',
					position: 'absolute',
					bottom: isLandscape ? '-5px' : '5px',
					margin: '0 auto',
					scale: isLandscape ? '0.4' : ''
				}">
					<div id="loading-container"
						:class="isLandscape ? 'loading-container-land' : 'loading-container-port'">
						<div id="progress-bar-container-port"
							:class="isLandscape ? 'progress-bar-container-land' : 'progress-bar-container-port'">
							<div class="progress-bar-background"></div>
							<div class="progress-bar-fill-container">
								<div id="progress-bar-fill-stripes" class="progress-bar-fill stripes "
									style="background-color: rgb(195, 32, 182); width: 10%;">
									<div class="top-highlight"></div>
									<div class="front-highlight"></div>
								</div>
							</div>
							<div class="progress-bar-outline border-inner"></div>
							<div class="progress-bar-outline border-outer"></div>
						</div>
						<p id="tips-text" class="text-port"><span id="tips-text-span"
								:style="{ fontSize: isLandscape ? '10px' : '12px' }">Loading game
								resourcese</span><span id="tips-text-child2"
								class="tips-text-child2 tips-text-child2-hidden">[10%]</span>
						</p>
					</div>

					<div id="animationTipsContainer" :class="isLandscape ? 'animationTipsContainer-land' : 'animationTipsContainer-port'">
						<div class="animated_text_wrap">
							<div id="animated_text" :class="[
								'animated_text',
								isLandscape
									? 'animated-text-reset-to-bottom-land'
									: 'animated-text-reset-to-bottom-port',
								'animated_text_move_to_center'
							]">
								Update to latest
								device update for best gaming experience!</div>
						</div>
					</div>
					<div id="__startedButton" :class="isLandscape
						? 'start-button-container-land start-button start-button-show-land'
						: 'start-button-container-port start-button start-button-show-port'">
						<div class="start-button-inner">
						</div>
						<p id="get-started-text" class="text-port" :style="{ fontSize: isLandscape ? '16px' : '12px' }">
							Começar</p>
					</div>
					<img class="footer-text-img" :src="`${imgUrl}/${brandName}/copyright.png`" id="footer-text-img"
						style="height: 40px; display: block; position: absolute; right: 50px; bottom: 25px;transform: scale(1.25)">

					<img id="footer-copyright-image-id" class="footer-copyright-image"
						:src="`${imgUrl}/${brandName}/website.png`"
						style="width: 80%; height: 8px; display: block; position: absolute; bottom: 0.5px; left: 10%;">
					</img>
				</div>
			</div>
		</div>
		<div id="initial-loader-1">
			<img class="initial-loader-img" :src="`${imgUrl}/${brandName}/循環.webp`" alt="" srcset="" />
		</div>
	</div>
</template>

<script>
import bgLandscape from '@/assets/image/landscape/startup.jpg';
import bgPortrait from '@/assets/image/portrait/startup.jpg';
export default {
	computed: {
		splashStyle() {
			const bgUrl = this.isLandscape ? bgLandscape : bgPortrait;

			return {
				position: "absolute",
				top: 0,
				left: 0,
				width: "360px",
				height: `${this.configurationHeight}px`,
				backgroundColor: "#171717",
				// backgroundImage: `url(${bgUrl})`,
				backgroundRepeat: "no-repeat",
				backgroundPosition: "center",
				backgroundSize: "cover",
			};
		}
	},

	components: {
	},
	data() {
		return {
			brandName: 'kf',
			gameId: null,
			timeStamp: '00:00:00',
			configurationHeight: 360,
			showHistory: false,
			hideHistory: false,
			showLoading: false,
			animationLoading: true,
			trophiesCoordinateBottom: 120,
			trophiesCoordinateLeft: 360,

			gameInfo: {
				jbicon_pos: "-999,-999",
				name: ""
			},

			isTrophiesIconShow: false,
			animationDuration: 0.75,

			imgUrl: process.env.VUE_APP_IMG_URL || 'https://testcdnupload.pg-gub.com/common',

			isLandscape: window.innerWidth > window.innerHeight,
		};
	},

	created() {

		this.brandName = this.getParams("brandName") || 'kf'
	},
	mounted() {
		this.updateConfigurationHeight();

		window.addEventListener("resize", this.onResize);
		window.addEventListener("orientationchange", this.onResize);
		window.addEventListener("resize", this.handleResize);

		let nav1 = document.createElement('script')
		nav1.type = 'text/javascript'
		nav1.src = require('@/assets/js/view.js')

		let nav = document.createElement('script')
		nav.type = 'text/javascript'
		nav.src = require('@/assets/js/game_index.js')

		setInterval(this.getCurrentTime, 1000)

		this.gameId = this.getParams("game_id")
		if (this.gameId) {
			this.getGame()
		}

		window.token = this.getParams("token")
		window.gameId = this.gameId

		localStorage.setItem("token", window.token)
		localStorage.setItem("gameId", window.gameId)

		this.configurationHeight = window.configurationHeight

		this.reloadAnimation();
	},
	beforeDestroy() {
		window.removeEventListener("resize", this.onResize);
		window.removeEventListener("orientationchange", this.onResize);
		window.removeEventListener("resize", this.handleResize);
	},

	methods: {
		reloadAnimation() {
			const img = this.$refs.animatedGif;
			const originalSrc = img.src;
			img.src = 'data:image/gif;base64,R0lGODlhAQABAIAAAP///wAAACH5BAEAAAAALAAAAAABAAEAAAICRAEAOw==';
			setTimeout(() => {
				img.src = `${originalSrc}?t=${Date.now()}`;
			}, 50);
		},
		updateWindowSize() {
			this.configurationHeight = window.configurationHeight
		},
		toVerifyByOrderNo(orderNo) {
			localStorage.setItem("orderNo", orderNo)
			let routeData = this.$router.resolve({
				name: "verify",
			});
			let uuid = this.guid().toUpperCase()
			let link =
				`https://verify.pgsoft.game/?atk=${uuid}&gid=68&env=13&l=pt&sid=1837027255981835264&tid=RCBQTF23&token=${token}&game_id=${window.gameId}&order_no=${orderNo}`
			window.open(link + routeData.href, '_blank');
		},
		guid() {
			return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (c) {
				var r = Math.random() * 16 | 0,
					v = c == 'x' ? r : (r & 0x3 | 0x8);
				return v.toString(16);
			});
		},
		changeLoading() {
			if (this.showLoading) {
				this.animationLoading = false
			}
			this.showLoading = !this.showLoading
		},
		getCurrentTime() {
			var _this = this;
			let hh = new Date().getHours();
			let mf = new Date().getMinutes() < 10 ? '0' + new Date().getMinutes() : new Date().getMinutes();
			let ss = new Date().getSeconds() < 10 ? '0' + new Date().getSeconds() : new Date().getSeconds();
			_this.timeStamp = hh + ':' + mf + ':' + ss;
		},
		getGame() {
			this.$http.get('/api/game/get_config', {
				params: {
					game_id: this.gameId
				}
			}).then(response => {
				if (response.data.meta.code == 0) {
					this.gameInfo = response.data.data
					if (this.gameInfo.jbicon_pos.length) {
						let trophiesCoordinate = this.gameInfo.jbicon_pos.split(",")
						this.trophiesCoordinateBottom = trophiesCoordinate[1]
						this.trophiesCoordinateLeft = trophiesCoordinate[0]
					}
					window.sessionStorage.setItem("trophiesCoordinateLeft", this.trophiesCoordinateLeft);
					window.sessionStorage.setItem("trophiesCoordinateBottom", this.trophiesCoordinateBottom);
				}
			});
		},
		getParams(name) {
			var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)");
			var r = decodeURI(window.location.search).substr(1).match(reg);
			if (r != null) {
				return unescape(r[2]);
			} else {
				return null;
			}
		},
		onResize() {
			this.isLandscape = window.innerWidth > window.innerHeight;
			this.updateConfigurationHeight();
		}
		,

		updateConfigurationHeight() {
			const width = window.innerWidth;
			const height = window.innerHeight;
			let finalWidth = width;
			let finalHeight = height;
			const ratio = width / height;

			if (this.isLandscape) {
				const designH = 750;
				const minW = 1334;
				const maxW = 1772;
				const minRatio = minW / designH;
				const maxRatio = maxW / designH;

				if (ratio < minRatio) {
					finalWidth = width;
					finalHeight = designH * (width / minW);
				} else if (ratio < maxRatio) {
					finalWidth = width;
					finalHeight = height;
				} else {
					finalWidth = height * (maxW / designH);
					finalHeight = height;
				}
			} else {
				const designW = 750;
				const minH = 1334;
				const maxH = 1638;
				const minRatio = designW / maxH;
				const maxRatio = designW / minH;

				if (ratio < minRatio) {
					finalWidth = width;
					finalHeight = maxH * (width / designW);
				} else if (ratio < maxRatio) {
					finalWidth = width;
					finalHeight = height;
				} else {
					finalWidth = designW * (height / minH);
					finalHeight = height;
				}
			}

			this.configurationHeight = (finalHeight / finalWidth) * 360;
		},

		handleResize() {
			this.isLandscape = window.innerWidth > window.innerHeight;
		}
	}
}
</script>