import bgLandscape from '../image/landscape/startup.jpg';
import bgPortrait from '../image/portrait/startup.jpg';


function calcSize() {
    const width = window.innerWidth;
    const height = window.innerHeight;

    let finalWidth = 0;
    let finalHeight = 0;

    const ratio = width / height;
    if (width > height) {
        const designH = 750;
        const minW = 1334;
        const maxW = 1772;

        const minRatio = minW / designH;
        const maxRatio = maxW / designH;

        if (ratio < minRatio) {
            // 适配最小宽度 → 高度扩展
            finalWidth = width;
            finalHeight = designH * (width / minW);
        } else if (ratio < maxRatio) {
            // 正常范围
            finalWidth = width;
            finalHeight = height;
        } else {
            // 适配最大宽度 → 高度固定，宽度缩放
            finalWidth =  maxW* (height / designH);
            finalHeight = height;
        }
    }
    else {
        const designW = 750;
        const minH = 1334;
        const maxH = 1638;

        const minRatio = designW / maxH;
        const maxRatio = designW / minH;

        if (ratio < minRatio) {
            // 适配最大高度
            finalWidth = width;
            finalHeight = maxH * (width / designW);
        } else if (ratio < maxRatio) {
            // 正常范围
            finalWidth = width;
            finalHeight = height;
        } else {
            // 适配最小高度
            finalWidth = designW * (height / minH);
            finalHeight = height;
        }
    }
    const h360 = (finalHeight / finalWidth) * 360;

    window.configurationHeight = `${h360}`;
    window.paytableContentHeight = `${h360 - 63}`;

    return {
        width: finalWidth,
        height: finalHeight,
        top: (height - finalHeight) / 2,
        left: (width - finalWidth) / 2,
        h360
    };
}


window.resetGameView = function resetGameView() {
    window.isLandscape = window.innerWidth > window.innerHeight;

    const size = calcSize();

    const div = document.getElementById("game-box");
    if (!div) return;

    div.style.width = `${size.width}px`;
    div.style.height = `${size.height}px`;
    div.style.top = `${size.top}px`;
    div.style.left = `${size.left}px`;

    // 背景阴影
    const shadowDiv = document.getElementById("canvas-shadow");
    if (shadowDiv) {
        shadowDiv.style.width = `${size.width}px`;
        shadowDiv.style.height = `${size.height}px`;
        shadowDiv.style.top = `${size.top}px`;
        shadowDiv.style.left = `${size.left}px`;
    }

    // Cocos overlay 缩放
    const overlay = document.getElementById("game-overlay");
    if (overlay) {
        overlay.style.left = `${size.left}px`;
        overlay.style.top = `${size.top}px`;
        overlay.style.transform = `scale(${size.width / 360}) rotate(0deg)`;
    }

    // -----------------------------------------
    // 按 360 高度体系统一处理高度
    // -----------------------------------------
    const fullH = `${size.h360}px`;
    const contentH = `${size.h360 - 63}px`;

    const applyHeight = (id, h) => {
        const el = document.getElementById(id);
        if (el) el.style.height = h;
    };

    applyHeight("splash", fullH);
    applyHeight("game-history-container", fullH);
    applyHeight("paytable-container", fullH);
    applyHeight("paytable-content", contentH);
    applyHeight("game-rules-container", fullH);
    applyHeight("rulesContent", contentH);
    applyHeight("ui_block", fullH);
    applyHeight("ghVrfPopupAnim-BG", fullH);
    applyHeight("loading_panel", fullH);

    // -----------------------------------------
    // 安全区 & trophies 坐标适配
    // -----------------------------------------
    const safeHeight = (size.h360 - 640) / 2;

    const socialWidget = document.getElementById("social_widget-container");
    if (socialWidget) {
        let trophiesBottom = parseInt(window.sessionStorage.getItem("trophiesCoordinateBottom") || 0);
        let trophiesLeft = parseInt(window.sessionStorage.getItem("trophiesCoordinateLeft") || 0);

        trophiesBottom += safeHeight;

        socialWidget.style.transform = `translate(${trophiesLeft}px,${trophiesBottom}px)`;
    }

    // -----------------------------------------
    // 底部通用背景
    // -----------------------------------------
    const genericView = document.getElementById("footer-main-tournament-view");
    if (genericView) {
        const bottom = size.h360 - 640;
        genericView.style.transform = `translate(0px, ${bottom}px)`;
    }

    const splash = document.getElementById("splash");
if (splash) {
    splash.style.backgroundImage = window.isLandscape
        ? `url(${bgLandscape})`
        : `url(${bgPortrait})`;
}
};

resetGameView();
window.addEventListener("resize", resetGameView);
