import Vue from 'vue'
import App from './App.vue'
import router from "./assets/js/router.js"
import service from '../src/assets/js/http.js'

Vue.prototype.$http = service


function setFaviconByBrand() {
	const urlParams = new URLSearchParams(window.location.search)
	const brandName = urlParams.get('brandName') || 'kf'
	document.title = `${brandName.toUpperCase()}Game`

	// public 下文件可直接通过路径访问
	const iconPath = `${process.env.VUE_APP_IMG_URL}/${brandName}/favicon.ico`

	let link = document.querySelector("link[rel~='icon']")
	if (!link) {
		link = document.createElement('link')
		link.rel = 'icon'
		document.head.appendChild(link)
	}
	link.href = iconPath + '?v=' + Date.now()
}

setFaviconByBrand()

// 引入 vue-i18n
import VueI18n from 'vue-i18n'
Vue.use(VueI18n)

// 语言包
const messages = {
  en: {
    message: {
      hello: 'Hello World',
      start: 'Start'
    }
  },
  pt: {
    message: {
      hello: 'Olá Mundo',
      start: 'Começar'
    }
  }
}

// 读取 URL 参数 ?language=
const urlParams = new URLSearchParams(window.location.search)
const languageParam = urlParams.get('language')
const supportedLangs = ['en', 'pt']
const language = supportedLangs.includes(languageParam) ? languageParam : 'pt'

// 创建 i18n 实例
const i18n = new VueI18n({
  locale: language,
  fallbackLocale: 'en',
  messages
})

// ✅ 提前挂到 window，确保外部 JS 可以访问
window.i18n = i18n

import VueScroll from 'vuescroll'
Vue.use(VueScroll)

Vue.config.productionTip = false

// 创建 Vue 实例
new Vue({
  router,
  i18n,
  render: h => h(App)
}).$mount('#app')
